import React from 'react'
import oregon from '../img/oregon.png'

const Fact3 = (props) => {
   
    return (
        <div>
            <div className="container center">
                <h4 className="top">Oregon</h4>
                        <img src={oregon} alt="or" id="oregon"/>
                        <ul className="list">
                            <li>Oregon and New Jersey are the only states without self-serve gas stations.</li>
                            <li>The hazelnut is Oregon’s official state nut. Oregon is the only state that has an official state nut. The hazelnut is also known as the filbert.</li>
                        </ul>

                    </div>
                </div>  
    )
}

export default Fact3;