import React from 'react'
import ohio from '../img/ohio.png'

const Fact2 = () => {
    return (
        <div>
            <div className="container center">
                <h4 className="top">Ohio</h4>
                        <img src={ohio} alt="oh" id="ohio"/>
                        <ul className="list">
                            <li>Cincinnati had the first professional city Fire Department.</li>
                            <li>Seven United States presidents were born in Ohio. They are: Ulysses S. Grant, Rutherford B. Hayes, James A. Garfield, Benjamin Harrison, William McKinley, William H. Taft, and Warren G. Harding.</li>
                        </ul>

                    </div>
                </div>  
    )
}

export default Fact2;