import React from 'react'
import { Link, NavLink, withRouter } from 'react-router-dom'

const Navbar = () => {

    return (
        <nav className='nav-wrapper brown darken-3'>
            <div className="container">
                <a href="/" className="brand-logo">Interesting Facts about States I've lived in!</a>
                <ul className="right">
                    <li><Link to='/'>Texas</Link></li>
                    <li><Link to='/Fact2'>Ohio</Link></li>
                    <li><NavLink to='/Fact3'>Oregon</NavLink></li>
                </ul>
            </div>
        </nav>
    )
}           

export default withRouter(Navbar)