import React, { Component } from 'react'
import Navbar from './components/Navbar'
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import Texas from './components/Fact1'
import Ohio from './components/Fact2'
import Oregon from './components/Fact3'


class App extends Component {
  render(){
    return (
      <BrowserRouter>
        <div className="App">
          <Navbar/>
            <Switch>
          <Route exact path='/' component={Texas}/>
          <Route exact path='/statefacts' component={Texas}/>
          <Route path='/Fact2' component={Ohio}/>
          <Route path='/Fact3' component={Oregon}/>
            </Switch>
        </div>
      </BrowserRouter>
    )
  } 
}

export default App;