import React from 'react'
import texas from '../img/texas.png'

const Fact1 = () => {
        
            return(
                <div>
                    <div className="container center">
                        <h4 className="top">Texas</h4>
                        <img src={texas} alt='tx' id="texas"/>
                        <ul className="list">
                            <li>Texas is the only state to have the flags of 6 different nations fly over it. They are: Spain, France, Mexico, Republic of Texas, Confederate States, and the United States.
                            </li>
                            <li>More wool comes from the state of Texas than any other state in the United States.</li>
                        </ul>
                    </div>
                </div>
            )
        }
    
       


export default Fact1